dule, applicationID, true, version);\n            const curModule = avaliableModulesData.value.find(i => i.ID == module.ID);\n            curModule!.relatedApplications.push({ applicationID, importedVersion: version });\n            const curModuleVersion = moduleVersions.value.find(modVersion => modVersion.version == version);\n            curModuleVersion!.relatedApplications!.push({applicationID, importedVersion: version});\n            selectedModule.value!.currentVersion = version;\n        }\n        else {\n            await studio.attachModuletoApplication(module, applicationID, false);\n            const curModule = avaliableModulesData.value.find(i => i.ID == module.ID);\n            curModule!.relatedApplications.push({ applicationID });\n        }\n        refreshTable()\n    }, { loading: true });\n}\n\nfunction remove(module: IModule, version?: string) {\n    executor.execute(async () => {\n        const applicationID = viewModel.studio.appId;\n        await studio.detachModuleFromApplication(module, applicationID, false, version);\n        const curModule = importedModulesData.value.find(i => i.ID == module.ID)!;\n        curModule.relatedApplications = curModule.relatedApplications.filter(i => i.applicationID != applicationID);\n        const curModuleVersion = moduleVersions.value.find(modVersion => modVersion.version == version);\n        curModuleVersion!.relatedApplications = curModule.relatedApplications.filter(i => i.applicationID != applicationID);\n        refreshTable()\n    }, { loading: true });\n}\n\nfunction isAddedtoCurrentApp(module: IModule) {\n    const appID = viewModel.studio.appId;\n    const isAdded = module.relatedApplications?.some(i => i.applicationID == appID);\n    return !!isAdded;\n}\n\nfunction selectModule(module: IModule) {\n    getVersions(module.ID);\n    selectedModule.value = module;\n    selectedModule.value.currentVersion = getImportedVersion(module);\n    importNewOrChange.value = true;\n}\nfunction getVersions(moduleID: string) {\n    executor.execute(async () => {\n        moduleVersions.value = await moduleVersionUseCase.listModuleVersions(moduleID);\n    }, {loading: true})\n}\nfunction getImportedVersion(module: IModule) {\n    const applicationID = viewModel.studio.appId;\n    const importedVersion = module.relatedApplications.find(app => applicationID == app.applicationID)?.importedVersion;\n    return importedVersion;\n}\nfunction computeImportedVersion() {\n    return selectedModule.value?.currentVersion;\n}\nfunction goBack() {\n    importNewOrChange.value = false;\n    refreshTable();\n}\n\n</script> \n \n<template>\n    <div class=\"container vue-table\">\n        <div v-if=\"!importNewOrChange\">\n            <div class=\"table-name\">\n                <span>Imported Modules</span>\n            </div>\n            <v-client-table ref=\"table\" :data=\"importedModulesData\" :columns=\"tableColumns\" :options=\"tableOptions\">\n                <template #createDate=\"props\">\n                    <span>{{ new Intl.DateTimeFormat(\"tr-TR\", dateOptions).format(new Date(props.row.createDate)) }}</span>\n                </template>\n                <template #ownerApp=\"props\">\n                    <span>{{ props.row.ownerApp }}</span>\n                </template>\n                <template #relatedAppNames=\"props\">\n                    <span>{{props.row.relatedAppNames}}</span>\n                </template>\n                <template #actions=\"props\">\n                    <div style=\"display: flex; align-items: center;\" >\n                        <div v-show=\" getImportedVersion(props.row)\" class=\"imported-version\" @click=\"selectModule(props.row)\">\n                            <i class=\"mdi mdi-pencil\" style=\"font-size: 16px; margin-right: 4px\"></i>\n                            <span>{{ getImportedVersion(props.row) }}</span>\n                        </div>\n                        <button class=\"remove-module\" @click=\"remove(props.row, getImportedVersion(props.row))\">\n                            <i class=\"mdi mdi-close-circle-outline\"></i>\n                            <span class=\"button-tooltip\" style=\"right: 48px; z-index: 1; width: 80px;\">Remove Module</span>\n                        </button>\n                    </div>\n                </template>\n            </v-client-table>\n            <div class=\"table-name\">\n                <span>Avaliable Modules</span>\n            </div>\n            <v-client-table ref=\"table\" :data=\"avaliableModulesData\" :columns=\"tableColumns\" :options=\"tableOptions\">\n                <template #createDate=\"props\">\n                    <span>{{ new Intl.DateTimeFormat(\"tr-TR\", dateOptions).format(new Date(props.row.createDate)) }}</span>\n                </template>\n                <template #ownerApp=\"props\">\n                    <span>{{ props.row.ownerApp }}</span>\n                </template>\n                <template #relatedAppNames=\"props\">\n                    <span>{{props.row.relatedAppNames}}</span>\n                </template>\n                <template #actions=\"props\">\n                    <button v-show = \"props.row.shareType == 'version' \" class=\"select-module-btn\"  @click=\"selectModule(props.row)\"><i class=\"mdi mdi-chevron-right\"></i></button>\n                    <button v-show = \"props.row.shareType == 'reference' \" class=\"add-ref-module-btn\"  @click=\"add(props.row)\">ADD</button>\n                </template>\n            </v-client-table>\n        </div>\n        <div v-if=\"importNewOrChange\">\n            <div class=\"module-name-div\" >\n                <i class=\"mdi mdi-arrow-left-box back-button\" @click=\"goBack()\"></i>\n                <span class=\"module-name\">{{ selectedModule?.name }}</span>\n            </div>\n            <div>\n                <span style=\"color: #212121; font-size: 14px\">Choose the module version you want to import.</span>\n            </div>\n            <v-client-table ref=\"table\" :data=\"moduleVersions\" :columns=\"moduleVersionTableColumns\" :options=\"moduleVersionTableOptions\">\n                <template #createDate=\"props\">\n                    <span>{{ new Intl.DateTimeFormat(\"tr-TR\", dateOptions).format(new Date(props.row.createDate)) }}</span>\n                </template>\n                <template #state=\"props\">\n                    <button v-if=\"props.row.version == computeImportedVersion()\" class=\"import-btn\" style=\"color: #D5D5D5; cursor: default;\">Imported</button>\n                    <button v-else class=\"import-btn\"  @click=\"add(selectedModule as IModule, props.row.version)\">Import</button>\n                </template>\n            </v-client-table>,\n        </div>\n    </div>\n</template> \n<style scoped>\n    @import url(\"../../../../styles/IDE3.css\");\n\n    .container {\n        padding: 32px 20px 20px;\n    }\n    .table-name {\n        color: #151233;\n        font-size: 16px;\n        font-weight: 500;\n        line-height: 18px;\n        margin-bottom: 12px;\n    }\n    .select-module-btn {\n        border: none;\n        background: none;\n        color: #212121;\n        font-size: 20px;\n    }\n    .back-button {\n        font-size: 28px;\n        color: #151233;\n        cursor: pointer;\n        margin-right: 16px;\n    }\n    .module-name-div {\n        display: flex;\n        align-items: center;\n        margin-bottom: 16px;\n    }\n    .module-name {\n        color: #151233;\n        font-size: 16px;\n        font-weight: 500;\n    }\n    .remove-module {\n        background: none;\n        border: none;\n        font-size: 20px;\n        color: #212121;\n        width: 100%;\n        display: flex;\n        justify-content: end;\n        align-items: center;\n    }\n    .imported-version {\n        color: #2C5A86;\n        font-size: 14px;\n        font-weight: 500;\n        margin-right: 22px;\n        cursor: pointer;\n    }\n    .import-btn {\n        background: none;\n 